import '../css/Login.css';
import React from 'react';
import { Link } from "react-router-dom";

export function Login() {
    return (
        < div className="contai" >

            <h1>Creative</h1>

            <div className="first"><input className="first_1" type="text" placeholder="Email or PhoneNumber" required />
                <input className="first_2" type="password" placeholder="password" maxlength="6" />
                <button className="first_3" type="submit" > <Link  to="/Sign">Submit</Link></button>

            </div>


        </div >
    )
}