import { setemployee, getemployee } from "../action/EmployeAction";

const initialState = {
    employee: {
        "Name": "Aurora",
        "Age": 53,
        "Email": "ante.blandit@disparturient.ca",
        "Street": "493 Iaculis Rd.",
        "City": "Lewiston",
        "Zip": "42591-180",
        "Region": "Maine",
        "Country": "Sudan",
        "Info": "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
        "EmployeeID": 101,
        "DateOfJoining": "10/06/2019",
        "PhoneNumber": "1628101902299"}

}

const EmployReduce = (state = initialState, action) => {
    
    switch (action.type) {
        case getemployee:
            return state;

        case setemployee:
            return {
                employee: action.payload //payload 
            };
            default:
                return state;
    }
}
export default EmployReduce;