    displaysrow() {
        let main = Object.values(this.props.employee);
        let main_1 = main.map(eachkeys => this.generatesbody(eachkeys));

        return this.generatesrow(main_1);
    }
    displayshead() {
        let heading = Object.keys(this.props.employee);
        let displayshead = heading.map(eachHead => this.generateshead(eachHead))

        return displayshead;
    }

    displaysbody(data) {
        let displayEachData = this.displaysrow(this.props.employee[data])
      
        return (displayEachData);
    }





