import '../css/SignUp.css';
import React from 'react';
export function Sign() {
    return (
        <div className="index">
            <h1>Sign Up</h1>
            <p><b>It's free and always will be</b></p>
            <div className="first">
                <input className="first_1" type="text" placeholder="FirstName" required />
                <input className="first_1" type="text" placeholder="LastName" required />
                <div>  <input className="second" type="number" placeholder="Mobile Number" required /></div>
                <div>  <input className="second" type="number" placeholder="Re-enter mobile number" required /></div>
                <div>  <input className="second" type="text" placeholder="New Password" required /></div>
            </div>
            <h4 className="birth">Birthday</h4>
            <div className="three">
                <div><input className="con" type="number" placeholder="month" required /></div>
                <div> <input className="con" type="number" placeholder="Day" required /></div>
                <div>  <input className="con" type="number" placeholder="Year" required /></div>
                <div>  <p className="pot">why do I need to provide my <br />birthday?</p></div>
            </div>
            <div className="rad"> <input type="radio" name="colour" /> male
                <input class="rad_1" type="radio" name="colour" /> female
            </div>
            <div className="sig">
                <button className="sig_1">Sign UP</button>
            </div>



        </div>
    )
}