import { INCREMENT, DECREMENT, COUNTER_RESET } from '../action/CounterAxtion';


const initialState = {
    count: 0
}

const CounterReduce = (state = initialState, action) => {
    switch (action.type) {
        case INCREMENT:
            return {
                count: state.count + 1
            };
        case DECREMENT:
            return {
                count: state.count - 1
            };
        case COUNTER_RESET:
            return {
                count: state.count = 0
            };
        default:
            return state;
    }
}
export default CounterReduce; 